import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HelpdeskSetupComponent } from './helpdesk-setup.component';
import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';
import { PrivateCategoryComponent } from './private-category/private-category.component';
import { CommonCategoryComponent } from './common-category/common-category.component';

const routes: Routes = [
	{ path: '', component: HelpdeskSetupComponent, canActivate: [AuthGuard],
		children: [
			{ path: '', redirectTo: 'private-category', canActivate: [AuthGuard], pathMatch: 'full'},
			{ path: 'private-category', component: PrivateCategoryComponent, canActivate: [AuthGuard]},
			{ path: 'common-category', component: CommonCategoryComponent, canActivate: [AuthGuard]}, 
			{ path: '**', redirectTo: 'private-category', pathMatch: 'full'}
		]
	},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HelpdeskSetupRoutingModule { }
